import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useAlert } from 'react-alert';
import { Helmet } from 'react-helmet';
const SignInForm = () => {
  const alert = useAlert();
  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    validationSchema: Yup.object({
      email: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          // eslint-disable-next-line
          /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/,
          'a ch email khng hp l'
        ),
      password: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,

          'Mt khu yu cu 8 k t, cha t nht mt ch ci v mt s '
        ),
    }),
    onSubmit: async (values) => {
      try {
        await axios.post('/user/login', { ...values });

        localStorage.setItem('firstLogin', true);

        window.location.href = '/';
      } catch (err) {
        alert.show(
          <div style={{ fontSize: '12px' }}>{err.response.data.msg}</div>
        );
      }
    },
  });

  return (
    <section>
      <Helmet>
        <meta charSet="utf-8" />
        <title>ng nhp</title>
        <link
          rel="canonical"
          href="http://mysite.com/example"
        />
        <meta
          name="description"
          content="Truong Van Tam dang dev Yolo"
        />
      </Helmet>
      <header>ng nhp</header>
      <form
        className="infoform"
        onSubmit={formik.handleSubmit}
      >
        <label> Email</label>
        <input
          type="email"
          id="email"
          name="email"
          placeholder="Email "
          value={formik.values.email}
          onChange={formik.handleChange}
        />
        {formik.errors.email && (
          <p className="errorMsg"> {formik.errors.email} </p>
        )}

        <label> Mt khu </label>
        <input
          type="text"
          id="password"
          name="password"
          placeholder="Mt khu"
          value={formik.values.password}
          onChange={formik.handleChange}
        />
        {formik.errors.password && (
          <p className="errorMsg"> {formik.errors.password} </p>
        )}
        <div className="infoform__control">
          <Link
            to="/signup"
            className="infoform__control__btn"
          >
            ng k
          </Link>

          <Link
            to="/forgot_password"
            className="infoform__control__forgot"
          >
            Forgot your password?
          </Link>
          <button type="submit"> Tip tc </button>
        </div>
      </form>
    </section>
  );
};

export default SignInForm;
