import React from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useAlert } from 'react-alert';
import { types } from 'react-alert';
import { Helmet } from 'react-helmet';
const SignupForm = () => {
  const alert = useAlert();
  const formik = useFormik({
    initialValues: {
      email: '',
      name: '',
      phone: '',
      password: '',
      confirmedPassword: '',
    },
    validationSchema: Yup.object({
      name: Yup.string()
        .required('Vui lng nhp trng ny')
        .min(4, 'Tn qu ngn, yu cu t nht 4 k t'),
      email: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          // eslint-disable-next-line
          /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/,
          'a ch email khng hp l'
        ),
      password: Yup.string()
        .required('Vui lng nhp trng ny')
        .matches(
          /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,

          'Mt khu yu cu 8 k t, cha t nht mt ch ci v mt s '
        ),
      confirmedPassword: Yup.string()
        .required('Vui lng nhp trng ny')
        .oneOf([Yup.ref('password'), null], 'Mt khu khng trng khp'),
    }),
    onSubmit: async (values) => {
      try {
        await axios.post('/user/register', { ...values });
        localStorage.setItem('firstLogin', true);
        alert.show(
          <div style={{ fontSize: '12px' }}>
            Chng ti va gi mt email xc nhn cho bn, vui lng xc nhn 
            hon tt ng k
          </div>,
          { type: types.INFO }
        );
      } catch (err) {
        alert.show(
          <div style={{ fontSize: '12px' }}>{err.response.data.msg}</div>
        );
      }
    },
  });

  return (
    <section>
       <Helmet>
        <meta charSet="utf-8" />
        <title>ng k</title>
        <link
          rel="canonical"
          href="http://mysite.com/example"
        />
        <meta
          name="description"
          content="Truong Van Tam dang dev Yolo"
        />
      </Helmet>
      <header>ng k</header>
      <form
        className="infoform"
        onSubmit={formik.handleSubmit}
      >
        <label> Tn khch hng </label>
        <input
          type="text"
          id="name"
          name="name"
          placeholder="H V Tn"
          value={formik.values.name}
          onChange={formik.handleChange}
        />
        {formik.errors.name && (
          <p className="errorMsg"> {formik.errors.name} </p>
        )}
        <label> Email</label>
        <input
          type="email"
          id="email"
          name="email"
          placeholder="Email "
          value={formik.values.email}
          onChange={formik.handleChange}
        />
        {formik.errors.email && (
          <p className="errorMsg"> {formik.errors.email} </p>
        )}

        <label> Mt khu </label>
        <input
          type="text"
          id="password"
          name="password"
          placeholder="Mt khu"
          value={formik.values.password}
          onChange={formik.handleChange}
        />
        {formik.errors.password && (
          <p className="errorMsg"> {formik.errors.password} </p>
        )}
        <label> Nhp li mt khu </label>
        <input
          type="text"
          id="confirmedPassword"
          name="confirmedPassword"
          placeholder="Xc nhn mt khu"
          value={formik.values.confirmedPassword}
          onChange={formik.handleChange}
        />
        {formik.errors.confirmedPassword && (
          <p className="errorMsg"> {formik.errors.confirmedPassword} </p>
        )}
        <div className="infoform__control">
          <Link
            to="/signin"
            className="infoform__control__btn"
          >
            ng nhp
          </Link>
          <button type="submit"> Tip tc </button>
        </div>
      </form>
    </section>
  );
};

export default SignupForm;
