import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import Grid from '../Layout/Grid';
import logo from '../../Asset/images/Logo.png';
import { GlobalState } from '../../GlobalState';
import { useTranslation } from 'react-i18next';
const footerSupportLink = [
  {
    display: 'Cc cu hi thng gp',
    path: '/about',
  },
  {
    display: 'Hng dn t hng',
    path: '/about',
  },
  {
    display: 'Phng thc vn chuyn',
    path: '/about',
  },
  {
    display: 'Hng dn tr gp',
    path: '/about',
  },
  {
    display: 'Chnh sch hng nhp khu',
    path: '/about',
  },
  {
    display: 'H tr khch hng: anhtamqwer79@gmail.com',
    path: '/about',
  },
  {
    display: 'Bo li bo mt: anhtamqwer79@gmail.com',
    path: '/about',
  },
];

const footerAboutLink = [
  {
    display: 'Gii thiu',
    path: '/about',
  },
  {
    display: 'Lin h',
    path: '/about',
  },
  {
    display: 'Tuyn dng',
    path: '/about',
  },
  {
    display: 'Tin tc',
    path: '/about',
  },
  {
    display: 'H thng ca hng',
    path: '/about',
  },
];

const footerCustomeLink = [
  {
    display: 'Chnh sch i tr',
    path: '/about',
  },
  {
    display: 'Chnh sch bo hnh',
    path: '/about',
  },
  {
    display: 'Chnh sch hon tin',
    path: '/about',
  },
];
const Footer = () => {
  const { t } = useTranslation();
  const state = useContext(GlobalState);
  const [theme] = state.theme;
  return (
    <footer
      className="container footer"
      id={theme}
    >
      <Grid
        col={4}
        mdCol={2}
        smCol={1}
        gap={10}
      >
        <div className="">
          <div className="footer__title">{t('H tr khch hng')}</div>
          <div className="footer__content">
            <p>
              Hotline: <a href="tel:0399817202">0399817202</a>
              <br />
              (1000 /{t('pht')}, 8-21h {t('k c')} T7, CN)
            </p>
            {footerSupportLink.map((item, index) => {
              return (
                <p key={index}>
                  <Link to={item.path}>{t(item.display)}</Link>
                </p>
              );
            })}
          </div>
        </div>
        <div>
          <div className="footer__title">{t('V Yolo')}</div>
          <div className="footer__content">
            {footerAboutLink.map((item, index) => {
              return (
                <p key={index}>
                  <Link to={item.path}>{t(item.display)}</Link>
                </p>
              );
            })}
          </div>
        </div>

        <div>
          <div className="footer__title">{t('Chm sc khch hng')}</div>
          <div className="footer__content">
            {footerCustomeLink.map((item, index) => {
              return (
                <p key={index}>
                  <Link to={item.path}>{t(item.display)}</Link>
                </p>
              );
            })}
          </div>
        </div>

        <div className="footer__about">
          <p>
            <Link to="/">
              <img
                src={logo}
                alt=""
                className="footer__logo"
              />
            </Link>
          </p>
          <p>
            {t(
              'Hng n mc tiu mang li nim vui n mc mi ngy cho hng triungi tiu dng Vit.' +
                'Hy cng Yolo hng n mt cuc sng nng ng, tch cc hn.' +
                ' t s hi lng ca khch hng l u tin s 1 trong mi suy ngh hnh ng ca mnh l s mnh, l trit l, chin lc.. lun cng YOLO tin bc'
            )}
          </p>
        </div>
      </Grid>
      <Grid
        col={1}
        mdCol={1}
        smCol={1}
      >
        <div className="footer__end">
          <span>
           {t('@ Bn quyn thuc v Yolo.vn All right reserved')}
          </span>
        </div>
      </Grid>
    </footer>
  );
};

export default Footer;
